.color-compare {
  position: fixed;
  right: 20px;
  top: 20px;
  width: 350px;
  border: 1px solid;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}
.color-compare > div {
  flex: 1 1 100%;
  padding: 10px;
}
.color-compare > div > div {
  height: 150px;
  width: 150px;
  border: 1px solid transparent;
  margin: 0 auto;
}
.color-compare > div input {
  margin: 5px auto 0;
  display: block;
  width: 100%;
}
.color-compare > div span {
  display: block;
  margin: 5px auto;
}

.color-family .row {
  display: flex;
  flex-wrap: nowrap;
}
.color-family .row > div {
  height: 32px;
  flex: 0 0 64px;
  border: 1px solid #dddddd;
  background-color: #dddddd;
}
.color-family .row.colors > div, .color-family .row.vivid > div {
  cursor: pointer;
}
.color-family .row.number > div {
  height: auto;
  border: 1px solid transparent;
}
.color-family .row.v > div:after {
  content: "v";
}

