.color-compare {
  position: fixed;
  right: 20px;
  top: 20px;
  width: 350px;
  border: 1px solid;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  > div {
    flex: 1 1 100%;
    padding: 10px;
    > div {
      height: 150px;
      width: 150px;
      border: 1px solid transparent;
      margin: 0 auto;
    }
    input {
      margin: 5px auto 0;
      display: block;
      width: 100%;
    }
    span {
      display: block;
      margin: 5px auto;
    }
  }
}

.color-family {
  .row {
    display: flex;
    flex-wrap: nowrap;
    > div {
      height: 32px;
      flex: 0 0 64px;
      border: 1px solid #dddddd;
      background-color: #dddddd;
    }
    &.colors,
    &.vivid {
      > div {
        cursor: pointer;
      }
    }
    &.number {
      > div {
        height: auto;
        border: 1px solid transparent;
      }
    }
    &.v > div:after {
      content: 'v';
    }
  }
}

